export default {
	data(){
		return{
			zxList:[],
			show: false,
			columns:[],
			chaoXiang:''
		}
	},
	onLoad() {
		this.getAssessApi()
	},
	methods:{
		getAssessApi(){
			var that = this;
			uni.request({
				url:'http://shop_api.fang-tian.com/api_fang/house/getEvaluationParam',
				method:'POST',
				success(res) {
					that.zxList = res.data.data.zhuangxiu
					let chaox = res.data.data.chaoxiang
					let arr = []
					for(let i=0;i<chaox.length;i++){
						arr.push(chaox[i].name)
					}
					that.columns = arr
				}
			})
		},
		// 朝向
		showPopup() {
		   this.show=true
		},
		onClose() {
		   this.show=false
		},
		 onConfirm(e) {
		    const { value, index } = e.detail;
			this.chaoXiang = e.target.value
			this.show=false
		  },
		
		  onCancel() {
			this.show=false
		  }
		
	}
}
